/* [1. 그리드 레이아웃 및 카드 - 기존 스타일 유지] */
.gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 360px);
    gap: 25px;
    margin-top: 20px;
}

.photo-card {
    background: white;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
    transition: all 0.3s ease;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    border: 1px solid #f0f0f0;
    height: 100%;
}

.photo-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15);
}

.img-placeholder {
    flex: 4;
    overflow: hidden;
    background-color: #f9f9f9;
}

.img-placeholder img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.photo-card:hover .img-placeholder img {
    transform: scale(1.1);
}

.photo-info {
    flex: 1;
    padding: 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: white;
}

.photo-title {
    margin: 0 0 5px 0;
    font-weight: 800;
    font-size: 1.1rem;
    color: #2c3e50;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.post-date {
    font-size: 0.85rem;
    color: #95a5a6;
}

/* 🚩 [2. 하단 컨트롤 영역 - Recommend 게시판 스타일 적용] */
.list-pagination-area {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 14px;
    width: 100%;
}

.page-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* 🚩 숫자 및 이전/다음 버튼: 완벽한 원형 스타일 */
.page-buttons button {
    width: 40px;
    /* Recommend 게시판 규격 */
    height: 40px;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 50% !important;
    /* 무조건 동그랗게 유지 */
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    color: #333;
    padding: 0;
}

/* 활성화된 페이지 (다크 네이비) */
.page-buttons button.active {
    background: #2c3e50 !important;
    color: #fff !important;
    border-color: #2c3e50 !important;
}

/* 호버 효과 */
.page-buttons button:hover:not(:disabled) {
    background-color: #f8f9fa;
    border-color: #2c3e50;
    color: #2c3e50;
}

/* 이전/다음 버튼이 비활성화(disabled) 되어도 공간은 유지됨 */
.page-buttons button:disabled {
    cursor: default;
    opacity: 0.4;
    background-color: #fdfdfd;
}

/* 🚩 [3. 검색창 및 푸터 액션 - Recommend 스타일] */
.footer-action-row {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4px;
    position: relative;
}

.search-footer {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-right: 40px;
}

.search-select-box {
    height: 46px;
    padding: 0 15px;
    border: 1px solid #ddd;
    border-radius: 30px;
    /* 알약 모양 */
    background-color: #fff;
    font-size: 0.9rem;
    color: #333;
    outline: none;
    cursor: pointer;
}

.search-input-wrapper {
    display: flex;
    align-items: center;
    height: 46px;
    border: 1px solid #ddd;
    border-radius: 30px;
    /* 알약 모양 */
    padding: 3px 5px 3px 15px;
    background: #fff;
}

.search-input-wrapper input {
    padding: 8px;
    border: none;
    outline: none;
    width: 250px;
    font-size: 0.9rem;
}

.btn-search {
    padding: 8px 22px;
    background: #2c3e50;
    color: #fff;
    border: none;
    border-radius: 20px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s;
    white-space: nowrap;
}

.btn-search:hover {
    background-color: #1a252f;
}

/* 🚩 [4. 후기 작성 버튼 - Recommend 스타일] */
.btn-write-footer {
    position: absolute;
    right: 0;
    padding: 12px 28px;
    background: #2c3e50;
    color: #fff;
    border: none;
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    white-space: nowrap;
}

.btn-write-footer:hover {
    background-color: #1a252f;
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

/* [4. 상세 페이지 래퍼 - 양옆 여백 통일] */
.event-detail-wrapper {
    padding: var(--page-padding-top) var(--page-padding-x) var(--page-padding-bottom);
    background-color: #f8f9fa;
    max-width: var(--page-size);
    min-height: calc(100vh - var(--header-height));
    margin: 0 auto;
}

@media (max-width: 768px) {
    .event-detail-wrapper {
        padding: 24px 20px 40px;
    }
}

/* [5. 상세 페이지 스타일 - 기존 스타일 유지] */
.detail-card {
    background: white;
    padding: 40px;
    border-radius: 20px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.05);
    max-width: 900px;
    margin: 0 auto;
}

.detail-title {
    font-size: 2rem;
    font-weight: 800;
    color: #2c3e50;
    margin-bottom: 15px;
}

.detail-meta {
    display: flex;
    gap: 15px;
    color: #95a5a6;
    margin-bottom: 30px;
    padding-bottom: 15px;
    border-bottom: 1px solid #f1f4f7;
}

.detail-content {
    line-height: 1.8;
    min-height: 350px;
}

.detail-content img {
    max-width: 100%;
    border-radius: 12px;
    margin: 20px 0;
}